import React, {Component} from 'react';
import {Switch, Route} from 'react-router-dom';

import Home from '../pages/Home';
import DetailContainer from '../containers/detailContainer';
import CartContainer from '../containers/cartContainer';
import CategoryContainer from '../containers/categoryContainer';
import CategoryDetailContainer from '../containers/categoryDetailContainer';
import SearchContainer from '../containers/searchContainer';
import ShippingContainer from '../containers/shippingContainer';
import ShippingPaymentContainer from '../containers/shippingPaymentContainer';
import ShippingReviewContainer from '../containers/shippingReviewContainer';
import CheckoutContainer from '../containers/checkoutContainer';
import WishlistContainer from '../containers/wishListContainer';

class routerComponents extends Component {
  render() {
    return (
      <Switch>
        <Route exact path="/" component={Home} />
        <Route
          exact
          path="/detail/:product_id"
          onUpdate={() => window.scrollTo(0, 0)}
          component={DetailContainer}
        />
        <Route exact path="/search" component={SearchContainer} />
        <Route exact path="/cart" component={CartContainer} />
        <Route exact path="/category" component={CategoryContainer} />
        <Route
          exact
          path="/category/:category_id"
          component={CategoryDetailContainer}
        />
        <Route exact path="/shipping" component={ShippingContainer} />
        <Route
          exact
          path="/shipping/select-payment"
          component={ShippingPaymentContainer}
        />
        <Route
          exact
          path="/shipping/review"
          component={ShippingReviewContainer}
        />
        <Route exact path="/checkout" component={CheckoutContainer} />
        <Route exact path="/wishlist" component={WishlistContainer} />
      </Switch>
    );
  }
}

export default routerComponents;



