import React from 'react';

import {Field, reduxForm} from 'redux-form';

let shippingPaymentForm = props => {
  const {handleSubmit} = props;

  return (
    <form onSubmit={handleSubmit}>
      <div className="shipping-section-select-payment">
        <b>Metode Pembayaran: </b>
        <div className="card">
          <div className="card-body">
            <div className="form-group">
              <div className="form-check border-bottom">
                <div className="row">
                  <div className="col-2 align-center">
                    <Field
                      component="input"
                      className="form-check-input"
                      type="radio"
                      name="paymentType"
                      id="radioBCA"
                      value="BCA"
                      aria-describedby="radioBCAHelp"
                    />
                  </div>
                  <div className="col-10">
                    <label className="form-check-label" htmlFor="radioBCA">
                      BCA
                    </label>
                    <small id="radioBCAHelp" className="form-text text-muted">
                      731-098-1234
                    </small>
                  </div>
                </div>
              </div>
              <div className="form-check border-bottom">
                <div className="row">
                  <div className="col-2 align-center">
                    <Field
                      component="input"
                      className="form-check-input"
                      type="radio"
                      name="paymentType"
                      id="radioMandiri"
                      value="MANDIRI"
                      aria-describedby="radioMandiriHelp"
                    />
                  </div>
                  <div className="col-10">
                    <label className="form-check-label" htmlFor="radioMandiri">
                      MANDIRI
                    </label>
                    <small
                      id="radioMandiriHelp"
                      className="form-text text-muted">
                      0700-000-869-792
                    </small>
                  </div>
                </div>
              </div>
            </div>

            <button type="submit" className="btn btn-success btn-block">
              Selanjutnya
            </button>
          </div>
        </div>
      </div>
    </form>
  );
};

shippingPaymentForm = reduxForm({
  form: 'selectPayment',
  initialValues: {paymentType: 'BCA'},
  enableReinitialize: true
})(shippingPaymentForm);

export default shippingPaymentForm;



