import homeConstants from '../_constants/homeConstants';

export default function (
  state = {
    fetched: false,
    products: [],
    category: '',
    sortBy: ''
  },
  action
) {
  switch (action.type) {
    case homeConstants.FETCHING:
      return {...state, fetched: false};
    case homeConstants.GET_PRODUCT:
      return { ...state,
        products: action.payload,
        fetched: true,
        category: '',
        sortBy: ''
      };
    case homeConstants.GET_SORTED_PRODUCT:
      return {...state,
        products: action.payload.products,
        fetched: true, 
        category: '',
        sortBy: action.payload.sortKey
      };
    case homeConstants.GET_PRODUCT_BY_CATEGORY:
      return { ...state,
        products: action.payload.filteredProducts,
        category: action.payload.category,
        fetched: true
      };
    default:
      return state;
  }
}



