import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {Link, withRouter} from 'react-router-dom';

import {endTransaction} from '../_actions/shippingActions';
import variableConstants from '../_constants/variableConstants';
class checkoutPage extends Component {
  constructor(props) {
    super(props);
    window.scrollTo(0, 0);
    const {history} = this.props;
    let sessionId = localStorage.getItem(variableConstants.SessionId);
    this.state = {
      expiredAt: null,
      orderNumber: null,
      totalTransfer: null,
      bankType: null,
      bankNumber: null
    };
    if (!sessionId) {
      history.push('/');
    }

    this.endTransaction = this.endTransaction.bind(this);
    // console.log(this.props.shippingReducers);
  }

  componentWillMount() {
    const {history} = this.props;
    const shippingDetail = JSON.parse(localStorage.getItem('shippingDetail'));
    const paymentData = JSON.parse(localStorage.getItem('paymentData'));
    const shippingRate = JSON.parse(localStorage.getItem('shippingRate'));
    const checkout = JSON.parse(localStorage.getItem('checkout'));

    let nf = Intl.NumberFormat();
    if (checkout != null) {
      this.setState({
        expiredAt: checkout.expired_at,
        orderNumber: checkout.order_number,
        totalTransfer: nf.format(checkout.total_transfer_unique),
        bankType: paymentData.type,
        bankNumber: paymentData.bankNumber
      });
    } else {
      history.push('/');
    }
  }

  endTransaction() {
    const {history} = this.props;

    this.props.actions.endTransaction(history);
  }

  render() {
    return (
      <div className="checkout-page">
        <div className="checkout-menu bg-light border-bottom">
          <div className="container">
            <div className="row bor">
              <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-xs-12">
                <div className="row">
                  <div className="col-12 align-center">
                    <b>CHECKOUT</b>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="checkout-content full">
          <div className="container">
            <div className="row">
              <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-sm-12 bg-light padding-small">
                <div className="checkout-header align-center padding-xsmall">
                  <div className="checkout-title color-gold">
                    CHECKOUT BERHASIL
                  </div>
                  <div className="checkout-help-title">
                    Pesanan anda telah kami terima
                  </div>
                </div>
                <div className="checkout-body align-center">
                  <div className="card">
                    <div className="card-body">
                      <div className="checkout-reminder">
                        <div className="checkout-reminder-title">
                          Mohon segera lakukan pembayaran sebelum:
                        </div>
                        <div className="checkout-reminder-deadline">
                          <b>{this.state.expiredAt}</b>
                        </div>
                      </div>

                      <div className="checkout-total">
                        <div className="row">
                          <div
                            id="total-price"
                            className="col-lg-8 offset-lg-2 col-md-8 offset-md-2 col-sm-8 offset-sm-2 bg-grey">
                            Rp. {this.state.totalTransfer}
                          </div>
                        </div>
                      </div>
                      <div className="checkout-message">
                        <div className="row">
                          <div
                            id="checkout-message"
                            className="col-12 bg-black">
                            Mohon transfer hingga 3 digit terakhir kode unik
                            untuk mempercepat proses verifikasi
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="checkout-body padding-top-bottom-small">
                  <div className="card">
                    <div className="card-body">
                      <div className="bank-account-header padding-top-bottom-small align-center">
                        Pembayaran dapat dilakukan melalui transfer ke rekening
                        PT Ebaba Muslim Indonesia berikut:
                      </div>
                      <div className="bank-account-list padding-top-bottom-small">
                        <div className="bacnk-account-detail border-bottom border-top">
                          <div className="row">
                            <div className="col-12 align-center">
                              <h5>
                                <b>{this.state.bankType}</b>
                              </h5>
                              <br />
                              <h5>
                                <b>{this.state.bankNumber}</b>
                              </h5>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="card-footer">
                      <div
                        className="btn btn-outline-dark btn-block"
                        onClick={this.endTransaction}>
                        CLOSE
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({endTransaction}, dispatch)
  };
}

const mapStateToProps = state => {
  return {
    shippingReducers: state.shippingReducers,
    cartReducers: state.cartReducers
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(checkoutPage)
);



