import React from 'react';

export default () => (
  <div className="card-action">
    <div className="d-inline color-soft-grey" style={{marginRight: '0.8rem'}}>
      <i
        className="mdi mdi-heart mdi-18px"
        style={{margin: '0.5rem 0.5rem 0.5rem 0'}}
      />Suka
    </div>
    <div className="d-inline color-soft-grey" style={{marginRight: '0.8rem'}}>
      <i
        className="mdi mdi-share-variant mdi-18px"
        style={{margin: '0.5rem 0.5rem 0.5rem 0'}}
      />Bagikan
    </div>
  </div>
);



