import React, {Component} from 'react';
import ImgCommunity from '../../static/img/bg-community.png';
import ImgCharity from '../../static/img/bg-charity.png';

class subsidiaries extends Component {
  render() {
    let bgCommunity = {
      backgroundImage: `url(${ImgCommunity})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPosition: 'center'
    };

    let bgCharity = {
      backgroundImage: `url(${ImgCharity})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'cover',
      backgroundPosition: 'center'
    };
    return (
      <div className="row subsidiaries-content no-gutters">
        <div className="col-6">
          <div
            className="subsidiaries-community rounded-border-gold padding-xsmall"
            style={bgCommunity}>
            <a
              href="//ebaba.co.id/komunitas"
              target="_blank"
              rel="noopener noreferrer">
              KOMUNITAS
            </a>
          </div>
        </div>
        <div className="col-6">
          <div
            className="subsidiaries-charity rounded-border-gold padding-xsmall "
            style={bgCharity}>
            <a
              href="//ebaba.co.id/berbagi"
              target="_blank"
              rel="noopener noreferrer">
              EBABA BERBAGI
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default subsidiaries;



