import React, {Component} from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {Link} from 'react-router-dom';
import ModalComponent from '../components/modal/modal';
import SortModal from './modal/SortModal';

const FilterMenu = ({
  clickToSort,
  sortActive,
  activateSort,
  dismissModal,
  sortName = '',
  compact = true
}) => (
  <div className="filter-menu bg-light border-bottom">
    <div className="container">
      <div className="row">
        <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-xs-12">
          <div className="row">
            {!compact && (
              <div className="col-4 col-sm-4 border-right filter-menu-col">
                <Link to="/category" className="color-gold">
                  <span className="filter-menu-item float-left">Kategori</span>
                  <i className="mdi mdi-apps mdi-24px float-right" />
                </Link>
              </div>
            )}

            <div
              className={classNames('border-right filter-menu-col', {
                ['col-4']: !compact,
                ['col-8']: compact
              })}
              onClick={activateSort}>
              <Link to="/#" className="filter-menu-col color-gold">
                <span className="filter-menu-item float-left">
                  {compact ? sortName : 'Urutkan'}
                </span>
                <i
                  className={classNames('mdi mdi-24px float-right', {
                    ['mdi-sort-alphabetical']: !compact,
                    ['mdi-menu-down']: compact
                  })}
                />
              </Link>
            </div>
            <div className="col-4 col-sm-4 filter-menu-col">
              <Link to="/#" className="color-gold">
                <span className="filter-menu-item float-left">Filter</span>
                <i className="mdi mdi-filter-variant mdi-24px float-right" />
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <SortModal
      active={sortActive}
      dismissModal={dismissModal}
      clickToSort={clickToSort}
    />
  </div>
);

FilterMenu.propTypes = {
  sortName: PropTypes.string,
  clickToSort: PropTypes.func.isRequired,
  sortActive: PropTypes.bool.isRequired,
  activateSort: PropTypes.func.isRequired,
  dismissModal: PropTypes.func.isRequired,
  compact: PropTypes.bool
};

export default FilterMenu;



