import React, {Component, Fragment} from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {SORT_VALUES} from '../_constants/sortConstants';
import {
  getAllProducts,
  getSortedProducts,
  fetching
} from '../components/home/actions/HomeActions';
import * as Components from '../components';
import SortModal from '../components/modal/SortModal';
import Layout from '../layout';

class HomePage extends Component {
  state = {
    allProducts: '',
    loading: true,
    sortActive: false
  }

  componentWillMount() {
    this.props.actions.getAllProducts(this.props.history).then(() => {
      this.setState({
        allProducts: this.props.homeReducers.products
      });
      this.props.homeReducers.products &&
        this.props.homeReducers.products.length !== 0 &&
        this.setState({loading: false});
    });
  }
  componentDidMount() {
    document.addEventListener('keydown', this.escModalListener, false);
  }
  componentWillUnmount() {
    document.removeEventListener('keydown', this.escModalListener, false);
  }
  clickToSort = e => {
    this.props.actions.fetching();
    this.setState({sortActive: false});
    return this.props.actions.getSortedProducts(e.target.value);
  }
  activateSort = () =>
    this.setState({
      sortActive: !this.state.sortActive
    });

  dismissSort = () => this.setState({sortActive: false});

  escModalListener = event => {
    if (event.keyCode === 27) {
      this.state.sortActive && this.dismissSort();
    }
  };

  render() {
    const filterMenuOptions = {
      sortActive: this.state.sortActive,
      activateSort: this.activateSort,
      clickToSort: this.clickToSort,
      dismissModal: this.dismissSort,
      compact: this.props.homeReducers.sortBy !== '',
      sortName:
        this.props.homeReducers.sortBy === ''
          ? 'Urutkan'
          : SORT_VALUES[this.props.homeReducers.sortBy.toUpperCase()].label
    };
    return (
      <Layout>
        {!this.props.homeReducers.fetched ? (
          <Components.Loader />
        ) : (
          <Fragment>
            <Components.filterMenu {...filterMenuOptions} />
            {this.props.homeReducers.sortBy === '' ? (
              <Fragment>
                <Components.contentWrapper
                  contentName="home-subsidiaries"
                  padding="padding-xsmall"
                  align="align-center">
                  <Components.subsidiaries />
                </Components.contentWrapper>
                <Components.contentWrapper contentName="home-slider">
                  <Components.HomeSlider />
                </Components.contentWrapper>

                <Components.contentWrapper contentName="home-stories">
                  <Components.CategoryStories />
                </Components.contentWrapper>
              </Fragment>
            ) : (
                <Components.contentWrapper contentName="home-stories">
                  <Components.CategoryStories withLink/>
                </Components.contentWrapper>
            )}

            <Components.contentWrapper contentName="home-content">
              <Components.Card products={this.props.homeReducers.products} />
            </Components.contentWrapper>
          </Fragment>
        )}
      </Layout>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(
    {getAllProducts, getSortedProducts, fetching},
    dispatch
  )
});
const mapStateToProps = state => {
  return {
    homeReducers: state.homeReducers
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(HomePage);



