import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import EbabaIcon from '../static/img/ebaba-icon.png';
import SearchForm from '../components/form/searchForm';

class homeMenu extends Component {
  render() {
    return (
      <div className="home-menu">
        <div className="row">
          <div
            className="col-2 col-sm-2"
            style={{margin: 'auto auto', textAlign: 'left'}}>
            <Link to="/" className="color-gold">
              <div className="home-icon" style={{width: '2rem'}}>
                <img className="img-fluid" src={EbabaIcon} alt="ebaba icon" />
              </div>
            </Link>
          </div>
          <div className="col-10 col-sm-10">
            <Link to="/search">
              <SearchForm />
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default homeMenu;



