import axios from 'axios';
import variableConstants from '../../../_constants/variableConstants';
import homeConstants from '../../../_constants/homeConstants';
import Service from "../../../shared/Service";
import {API_PRODUCT} from "../../../shared/constant/endpointsConstant";
import {GET_PRODUCT} from "../constant";
export function fetching() {
  console.log('fetching..')
  return dispatch => dispatch({
    type: homeConstants.FETCHING,
  });
}

export function getAllProducts(history) {
  return dispatch =>
    Service.request({url: API_PRODUCT}).then(products =>
      dispatch({
        type: GET_PRODUCT,
        payload: products.data
      })
    );
}

export function getSortedProducts(sortKey) {
  //TODO: implement pagination
  return dispatch =>
    axios.get(`${variableConstants.URL}/customer/products/sort?filter=${sortKey}&per_page=1000&page=0`)
    .then(products => {
      if (products.data.data.length !== 0) {
        return dispatch({
          type: homeConstants.GET_SORTED_PRODUCT,
          payload: {
            products: products.data.data,
            sortKey
          }
        });
      }
    }).catch(err => console.error(err.response.data.message));
}
export function getProductsByCategory(category) {
  return dispatch =>
    axios.get(`${variableConstants.URL}/customer/products/`)
    .then(products => {
      const filteredProducts = products.data.data.filter(product => {
        for (let productCategory of product.category) {
          if (productCategory.name.toLowerCase() === category.toLowerCase()) {
            return product;
          }
        }
      })
      if (products.data.data.length !== 0) {
        return dispatch({
          type: homeConstants.GET_PRODUCT_BY_CATEGORY,
          payload: {
            filteredProducts,
            category
          }
        });
      }
    })
    .catch(err => console.error(err.response.data.message));
}



