import React from 'react';
import Slider from 'react-slick';

import Img1 from '../../static/img/banner-1.png';
import Img2 from '../../static/img/banner-2.png';
import Img3 from '../../static/img/banner-3.png';

const HomeSlider = ({settings}) => (
  <div className="slider-container">
    <Slider {...settings}>
      <div className="slider-item image-thumb">
        <img className="img-fluid" src={Img1} alt="1" />
      </div>
      <div className="slider-item image-thumb">
        <img className="img-fluid" src={Img3} alt="2" />
      </div>
    </Slider>
  </div>
);

HomeSlider.defaultProps = {
  settings: {
    arrows: false,
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  }
};

export default HomeSlider;



