import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import Card from './Card';
import CardImage from './CardImage';
import CardListGrid from './CardListGrid';
import CardListColumn from './CardListColumn';

const CardList = ({products, grid = false}) => {
  const sProducts = _.shuffle(products);
  return grid 
    ? <CardListGrid products={sProducts} />
    : <CardListColumn products={sProducts} />;
};

CardList.propTypes = {
  products: PropTypes.array.isRequired,
  grid: PropTypes.bool
};

export default CardList;



