import React from 'react';
import {connect} from 'react-redux';
import ProductSlider from './ProductSlider';
import ImageSlider from '../Slider/ImageSlider';

const ProductDetail = props => {
  let nf = Intl.NumberFormat();
  const {addToCart, handleColorChange} = props;
  const {product, variantSize, variantColor} = props.detailReducers;
  return (
    <div className="card card-no-radius card-noborder">
      <div className="card-header bg-light align-center no-padding ">
        <div className="card-img">
          {product.primary_image != null ? (
            product.images != null ? (
              <ImageSlider>
                {product.images.map(productImage => {
                  return (
                    <ProductSlider
                      productImage={productImage}
                      key={productImage.id}
                    />
                  );
                })}
              </ImageSlider>
            ) : (
              <img
                className="img-fluid"
                src={product.primary_image.original_path}
                alt="Product 1"
              />
            )
          ) : (
            <img
              className="img-fluid"
              src="https://i.pinimg.com/originals/88/2a/f9/882af9c0182e042783837a9deb7c4c35.jpg"
              alt="item not found"
            />
          )}
        </div>
      </div>

      <div className="card-body" style={{
        padding: '1.25rem 0'
      }}>
        <div className="detail-product-header">
          <div className="row no-gutters">
            <div className="col-8">
              <div className="detail-title-price">
                <div className="detail-product-title primary-title ">
                  {product.name}
                </div>
                <div className="detail-product-price primary-price color-gold">
                  Rp {nf.format(product.display_price)}
                </div>
              </div>
            </div>
            <div className="col-2 align-center">
              <div className="btn btn-circle circle-btn-shadow no-padding ">
                <div className="mdi mdi-heart mdi-24px" />
              </div>
              <div className="title-btn-circle">Suka</div>
            </div>
            <div className="col-2 align-center">
              <div className="btn btn-circle circle-btn-shadow no-padding ">
                <div className="mdi mdi-share-variant mdi-24px" />
              </div>
              <div className="title-btn-circle">Bagikan</div>
            </div>
          </div>
        </div>
        <div className="horizontal-line" />
        <div className="detail-product-desc primary-desc ">
          {product.description}
        </div>
        <div className="horizontal-line" />
        <div className="product-varian">
          <div className="product-varian-color padding-top-bottom-small">
            {variantColor.length > 0 ? (
              <div className="primary-title">Warna Pilihan</div>
            ) : null}
            <div className="btn-group btn-group-toggle" data-toggle="buttons">
              {variantColor.length > 0
                ? variantColor.map((color, index) => {
                    return (
                      <label
                        key={index}
                        onClick={handleColorChange}
                        id={color.id}
                        className="btn btn-sm btn-outline-info btn-square product-varian-btn margin-small">
                        <input
                          type="radio"
                          name="options_color"
                          id="option1"
                          autoComplete="off"
                        />
                        {color.value}
                      </label>
                    );
                  })
                : null}
            </div>
          </div>
          <div className="product-varian-size padding-top-bottom-small">
            {variantSize.length > 0 ? (
              <div className="primary-title">Ukuran ready stock</div>
            ) : null}
            <div className="btn-group btn-group-toggle" data-toggle="buttons">
              {variantSize.length > 0
                ? variantSize.map((size, index) => {
                    return (
                      <div
                        key={index}
                        onClick={this.handleSizeChange}
                        id={size.id}
                        className="btn btn-sm btn-outline-primary btn-square product-varian-btn margin-small">
                        <input
                          type="radio"
                          name="options_size"
                          value={size.id}
                        />
                        {size.value}
                      </div>
                    );
                  })
                : null}
            </div>
          </div>
        </div>
      </div>
      <div className="card-footer detail-page-btn bg-light" style={{
        padding: '1.25rem 0'
      }}>
        <button
          className="btn btn-block btn-success btn-lg bg-green"
          style={{
            borderRadius: '2px',
            border: 'none',
            fontWeight: 500,
            background: '#379f7a',
            padding: '13px 0'
          }}
          onClick={addToCart}>
          Beli Sekarang
        </button>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    detailReducers: state.detailReducers,
    cartReducers: state.cartReducers
  };
};

export default connect(mapStateToProps)(ProductDetail);



