import config from 'config';
import Request from 'axios';

const RequestType = {
  JSON: 'json',
  TEXT: 'text'
};

class Service {
  static request({
    method,
    url,
    headers = {},
    requestBody = {},
    requestType = RequestType.JSON
  }) {
    const params = {
      baseUrl: config.get("apiHost"),
      url,
      method,
      headers: {
        ...headers
      },
      responseType:
        requestType === RequestType.JSON ? RequestType.JSON : RequestType.TEXT
    };
    if (requestBody) {
      if (method === 'get') {
        const error = new Error('Method get cannot have requestBody');
        return Promise.reject(error);
      }
    }
    return Request(params)
      .then(res => {
        if (requestType === RequestType.JSON) {
          return res.data;
        } else {
          return res;
        }
      })
      .catch(error => {
        // format message so dev can react accordingly
        if (error.response) {
          throw new Error(
            `Fetching ${url} failed with status code ${error.response.status}`
          );
        }
        // Something happened in setting up the request that triggered an Error
        throw new Error(
          `Network error when requesting ${url}: ${error.message}`
        );
      });
  }
}

export default Service;



