import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import * as actions from '../components/home/actions/HomeActions';
import * as Components from '../components';

class wishListPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allProducts: '',
      loading: true
    };
  }

  componentWillMount() {
    this.props.getAllProducts(this.props.history).then(
      setTimeout(() => {
        this.setState({allProducts: this.props.homeReducers.products});
        this.setState({loading: false});
      }, 1000)
    );
  }
  render() {
    return (
      <div className="wishlist-page">
        <Components.topMenu>
          <Components.homeMenu />
        </Components.topMenu>
        {this.state.loading ? (
          <Components.Loader loading={this.state.loading} />
        ) : (
          <Components.contentWrapper contentName="page-content">
            <Components.Card allProducts={this.state.allProducts} />
          </Components.contentWrapper>
        )}
        <Components.bottomMenu />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    homeReducers: state.homeReducers
  };
};
export default withRouter(connect(mapStateToProps, actions)(wishListPage));



