import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {withRouter} from 'react-router-dom';

import * as Components from '../components';
import ProductDetail from '../components/ProductDetail';

import {getDetailProduct} from '../_actions/detailActions';
import {addItemToCart} from '../_actions/cartActions';
import {getAllProducts} from '../components/home/actions/HomeActions';

class DetailPage extends Component {
  state = {
    allProducts: '',
    productId: '',
    selectedSize: '',
    selectedColor: '',
    loading: true
  };

  componentWillMount() {
    // get product id
    const {match} = this.props;
    const id = match.params.product_id;
    this.setState({productId: id});

    this.props.actions.getAllProducts(this.props.history);
    // get detail assets
    this.props.actions.getDetailProduct(id, this.props.history).then(
      setTimeout(() => {
        // get all products
        this.setState({allProducts: this.props.homeReducers.products});
        this.setState({loading: false});
      }, 1000)
    );
  }

  componentWillReceiveProps(newProps) {
    const newId = newProps.match.params.product_id;
    if (this.props.detailReducers.product.id !== newId) {
      this.setState({loading: true});
      this.props.actions.getDetailProduct(newId, history).then(
        setTimeout(() => {
          this.setState({loading: false});
        }, 1000)
      );
      window.scrollTo(0, 0);
    }
  }

  handleSizeChange = e => {
    e.preventDefault();
    this.setState({selectedSize: e.target.id});
  };
  handleColorChange = e => {
    e.preventDefault();
    this.setState({selectedColor: e.target.id});
  };

  handleSubmit = () => {
    const {history} = this.props;
    const productData = {
      product_id: this.state.productId,
      qty: 1,
      size_id: this.state.selectedSize,
      color_id: this.state.selectedColor
    };
    // fire actions to add item to cart state
    this.props.actions.addItemToCart(productData, history);
  };

  render() {
    let nf = Intl.NumberFormat();
    const {detailFetched, product} = this.props.detailReducers;
    const pageId = window.location.pathname.replace('/detail/','');
    return (
      <div className="detail-page">
        {detailFetched &&
        product.id === pageId ? (
          <div>
            <Components.topMenu>
              <Components.pageMenu>Detail Produk</Components.pageMenu>
            </Components.topMenu>
            <Components.contentWrapper
              contentName="detail-content"
              padding="no-padding-xs">
              <ProductDetail
                addToCart={this.handleSubmit}
                handleColorChange={this.handleColorChange}
              />
            </Components.contentWrapper>
            <div className="container">
              <div className="row">
                <div className="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-sm-12 bg-light ">
                  <b>Produk Lainya</b>
                  <Components.Card products={this.props.homeReducers.products}/>
                </div>
              </div>
              <Components.bottomMenu />
            </div>
          </div>
        ) : (
          <Components.Loader />
        )}
      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(
      {getDetailProduct, addItemToCart, getAllProducts},
      dispatch
    )
  };
}

const mapStateToProps = state => {
  return {
    homeReducers: state.homeReducers,
    detailReducers: state.detailReducers,
    cartReducers: state.cartReducers
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(DetailPage)
);



