import React from 'react';
import PropTypes from 'prop-types';
import CardImage from './CardImage';
import CardDetail from './CardDetail';

const Card = ({product}) => (
  <div className="card" key={product.id}>
    <div className="card-header  card-img-header no-padding">
      <CardImage
        primaryImage={product.primary_image}
        images={product.images}
        subProducts={product.sub_products}
        id={product.id}
      />
    </div>
    <CardDetail description={product.description} />
  </div>
);

Card.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.string,
    primary_image: PropTypes.object,
    sub_products: PropTypes.array,
    images: PropTypes.arrayOf(
      PropTypes.shape({
        original_path: PropTypes.string
      })
    ),
    description: PropTypes.string
  })
};

export default Card;



