import React, {Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import CardTag from './CardTag';

class CardImage extends Component {
  state = {
    active: false
  };
  componentWillMount() {
    this.props.subProducts && this.props.withBuyTag && this.props.subProducts.length !== 0
      ? this.setState({
          active: false
        })
      : this.setState({active: true});
  }
  toggleActive = () => this.setState({active: true});

  render() {
    const {primaryImage, images, subProducts, id, withBuyTag} = this.props;
    return (
      <div className="card-img" onClick={this.toggleActive}>
        <div className="wrapper">
          {withBuyTag &&
            subProducts &&
            subProducts.length !== 0 &&
            this.state.active &&
            subProducts.map(sub_product => {
              return (
                <Link to={`/detail/${sub_product.id}`} key={sub_product.id}>
                  <CardTag
                    price={sub_product.display_price}
                    title={sub_product.name}
                    horizontalPos={
                      sub_product.tag_x_pos !== null
                        ? sub_product.tag_x_pos
                        : '50%'
                    }
                    verticalPos={
                      sub_product.tag_y_pos !== null
                        ? sub_product.tag_y_pos
                        : '50%'
                    }
                  />
                </Link>
              );
            })}
          {this.state.active ? (
            primaryImage != null ? (
              <Link to={`/detail/${id}`}>
                <img
                  className="img-fluid"
                  src={primaryImage.original_path}
                  alt="Product 1"
                />
              </Link>
            ) : (
              <img
                className="img-fluid"
                src="https://i.pinimg.com/originals/88/2a/f9/882af9c0182e042783837a9deb7c4c35.jpg"
                alt="item not found"
              />
            )
          ) : (
            <img
              className="img-fluid"
              src={primaryImage.original_path}
              alt="Product 1"
            />
          )}
        </div>

        {withBuyTag && (
          <div className="btn btn-default btn-sm card-btn">
            <span className="mdi mdi-basket" /> Tap untuk beli
          </div>
        )}
      </div>
    );
  }
}

CardImage.defaultProps = {
  withBuyTag: true
};

export default withRouter(CardImage);



